@charset "utf-8";

* {margin: 0;padding: 0;list-style: none;border: 0;-webkit-font-smoothing: antialiased;}
html, body {width: 100%;height: 100%;}
html {-webkit-text-size-adjust: none;font-size: 10px;line-height: 1;letter-spacing: -1px;}
html:lang(en) {letter-spacing: 0;}
body {font-family: 'Noto Sans KR', 'Titillium Web', '나눔고딕', NanumGothic, 'Nanum Gothic', sans-serif;color: #666766;font-size: 1.8rem;word-break: keep-all;word-wrap: break-word;}
table, .tb {display: table;width: 100%;table-layout: fixed;border-spacing: 0;border-collapse: collapse;font-family: inherit;}
.tb > * {display: table-cell;vertical-align: middle;text-align: center;padding: 5px;}
button {font-family: inherit;font-size: inherit;vertical-align: top;background: none;cursor: pointer;box-sizing: border-box;outline: 0;}
a {color: inherit;text-decoration: none;outline: 0;}
em, i {font-style: normal;}
iframe {width: 100%;height: 100%;}
img {vertical-align: top;max-width: 100%;height:auto;}
legend {display: none;}
label {cursor: pointer;}
input {font-family: inherit;color: inherit;line-height: inherit;vertical-align: middle;padding: 6px;border: 1px solid #d9d9d9;box-sizing: border-box;border-radius: 0;}
input::-webkit-input-placeholder {color: #ddd;}
input::-moz-placeholder {color: #ddd;}
input::-moz-placeholder {color: #ddd;}
input:-ms-input-placeholder {color: #ddd !important;}
input:-moz-placeholder {color: #ddd;}
textarea {width: 100%;color: inherit;font-family: inherit;line-height: inherit;padding: 6px;border: 1px solid #d9d9d9;resize: vertical;box-sizing: border-box;}
textarea::-webkit-input-placeholder {color: #cdcdcd;}
textarea:-ms-input-placeholder {color: #cdcdcd !important;}
textarea:-moz-placeholder {color: #cdcdcd;}
select {color: inherit;line-height: inherit;padding: 6px;border: 1px solid #d9d9d9;}
[tabindex] {outline: 0;}

input.chk {display: none;}
input.chk + label {padding-left: 3.3rem;}
input.chk + label:before {content: "";display: inline-block;vertical-align: middle;margin-right: 1rem;width: 2rem;height: 2rem;border: 1px solid #d3d4d4;transition: .5s;}
input.chk:checked + label:before {content: "";border: 1px solid #d3d4d4;background-color: #000;box-shadow: 0 0 0 .4rem #fff inset;}

.content :not(.pop_wrap)::-webkit-scrollbar {width: 7px;height: 7px;background: transparent;}
.content :not(.pop_wrap)::-webkit-scrollbar-button:start:decrement,
.content :not(.pop_wrap)::-webkit-scrollbar-button:end:increment {display: block;background: transparent}
.content :not(.pop_wrap)::-webkit-scrollbar-track {background: transparent;-webkit-border-radius: 10px;border-radius: 10px;}
.content :not(.pop_wrap)::-webkit-scrollbar-thumb {height: 50px;width: 50px;background: rgba(0, 0, 0, .2);-webkit-border-radius: 8px;border-radius: 8px;-webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, .1);}

