/* 해더 */
.gnb-on #header {z-index: 40;}
#header {position: fixed;top: 0;left: 0;right: 0;z-index: 40;text-align: center;min-height: 92px;box-sizing: border-box;transition: background-color .5s;background-color: rgba(255, 255, 255, 1);font-size: 0;}

#header .logo {position: relative;width: 160px;height: 100%;z-index: 50;margin-left: 112px;}
#header .logo a {display: block;margin: 24px 10px 0;width: 114px;height: 42px;background: url(../img/icons.png) 0 0 no-repeat;}

.nav_wrap .lang{position: absolute;top: 0;right: 160px;z-index: 1;display: block;font-size: 1.8rem;padding: 38px 10px 40px;line-height: 1;font-weight: bold;vertical-align: top;}
.nav_wrap .lang>a{position: relative;display:inline-block;padding-left: 14px;padding-right: 14px;font-size: 1.6rem;color: #000;font-weight: normal;transition: color .5s .1s;}
.nav_wrap .lang>a:before{content:"";position: absolute;top: 50%;left: 50%;width:0;height: 0;background-color: #000;transform: scale(0) translate(-50%,-50%);transform-origin:0 0;transition:transform .5s;}
.nav_wrap .lang>a.on{color: #fff;font-weight: bold;}
.nav_wrap .lang>a.on:before{width:100%;height: 22px;border-radius:10px;transform: scale(1) translate(-50%,-45%);}
.nav_wrap .lang{width:auto;}
.nav_wrap .lang:before{background-color: transparent !important;}
.nav_wrap .lang a{width:auto;}
.nav_wrap .lang a~a{margin-left:4px;}

.gnb_all_wrap .lang {display: none;position: absolute;top: 3.4rem;font-size: 1.8rem;line-height: 1;font-weight: bold;vertical-align: top;}
.gnb_all_wrap .lang > a {position: relative;display: inline-block;padding-left: 1.4rem;padding-right: 1.4rem;font-size: 1.6rem;color: #000;font-weight: normal;transition: color .5s .1s;}
.gnb_all_wrap .lang > a:before {content: "";position: absolute;top: 50%;left: 50%;width: 0;height: 0;background-color: #000;transform: scale(0) translate(-50%, -50%);transform-origin: 0 0;transition: transform .5s;}
.gnb_all_wrap .lang > a.on {color: #fff;font-weight: bold;}
.gnb_all_wrap .lang > a.on:before {width: 100%;height: 22px;border-radius: 10px;transform: scale(1) translate(-50%, -45%);}
.gnb_all_wrap .lang span {position: relative;}
.gnb_all_wrap .lang:before {background-color: transparent !important;}
.gnb_all_wrap .lang a {width: auto;}
.gnb_all_wrap .lang a ~ a {margin-left: .4rem;}

.fp-enabled #header .gsearch .ibtn_gsearch_toggle {transition: .5s;}
.fp-enabled #header .gsearch .gs_inwrap {transition: .5s .5s;}
#header .gsearch {position: absolute;top: 20px;right: 133px;z-index: 10;}
#header .gsearch .ibtn_gsearch_toggle {width: 68px;height: 58px;position: absolute;right: 0;top: 0;border: 0;}
#header .gsearch .ibtn_gsearch_toggle:before {width: 27px;height: 27px;transform: scale(1) translate(-50%, -50%);background-image: none;}
#header .gsearch.on .ibtn_gsearch_toggle:before {}
#header .gsearch .ibtn_gsearch_toggle i {position: absolute;bottom: 0;right: 0;width: 100%;height: 100%;}
#header .gsearch .ibtn_gsearch_toggle i:before {content: "";position: absolute;top: 0;left: 0;width: 100%;height: 100%;opacity: 0;transform: scale(0);background-color: #fff;transition: .5s 1s;box-shadow: 0 -10px 10px 2px rgba(0, 0, 0, .1);}
#header .gsearch .ibtn_gsearch_toggle i:after {content: "";position: absolute;top: 50%;left: 50%;width: 27px;height: 27px;transform: scale(1) translate(-50%, -50%);transform-origin: 0 0;transition: transform .5s 1s;background: url(../img/icons.png) -122px -6px no-repeat;}
.fp-viewing-1 #header .gsearch .ibtn_gsearch_toggle i:after {}
.fp-viewing-1.gnb-on #header .gsearch .ibtn_gsearch_toggle i:after {background-position: -122px -6px;transition: 0s 0s;}
#header .gsearch.on .ibtn_gsearch_toggle i {transform: scale(1);width: 100%;height: 100%;}
#header .gsearch.on .ibtn_gsearch_toggle i:before {transform: scale(1);opacity: 1;transition: .5s 0s;}
#header .gsearch.on .ibtn_gsearch_toggle i:after {transform: scale(0) translate(-50%, -50%);transition: transform .5s 0s;}
#header .gsearch .ibtn_gsearch_toggle:before {content: '';width: 50%;height: 2px;position: absolute;left: 25%;top: 50%;z-index: 1;background-color: transparent;transform: rotate(45deg);transition: background-color .5s .5s;}
#header .gsearch .ibtn_gsearch_toggle:after {content: '';width: 50%;height: 2px;position: absolute;left: 25%;top: 50%;z-index: 1;background-color: transparent;transform: rotate(-45deg);transition: background-color .5s .5s;}
#header .gsearch.on .ibtn_gsearch_toggle:before,
#header .gsearch.on .ibtn_gsearch_toggle:after {background-image: none;transition: background-color .5s .5s;background-color: #000;}
#header .gsearch .gs_inwrap {position: absolute;top: 58px;right: 0;padding: 52px;background-color: #fff;transform: scaleY(0);transform-origin: 50% 0;overflow: hidden;transition: .5s .5s;}
#header .gsearch.on .gs_inwrap {transform: scaleY(1);box-shadow: 0 10px 10px 2px rgba(0, 0, 0, .1);}
#header .gsearch input {width: 568px;margin: 0;padding: 8px 50px 8px 6px;font-size: 2.4rem;border-width: 0 0 2px 0;border-style: solid;border-color: #000;background-color: #fff;}
#header .gsearch .ibtn_gsearch {display: inline-block;margin-left: -37px;width: 27px;height: 27px;vertical-align: text-bottom;}
#header .gsearch .ibtn_gsearch:before {width: 27px;height: 27px;background-position: -122px -6px;}

.menu {overflow: hidden;position: fixed;top: 0;right: 0;display: block;width: 112px;height: 92px;background: none;outline: 0;z-index: 70;border: 1px solid rgb(226 225 226);border-width: 0 0 1px 1px;}
.menu i {overflow: hidden;position: absolute;top: 50%;left: 50%;display: block;width: 30px;height: 24px;background: none;outline: 0;z-index: 50;transform: translate(-50%, -50%);}
.menu i:before,
.menu i:after {content: "";position: absolute;left: 0;display: block;width: 100%;height: 3px;background-color: #333;-webkit-transition: -webkit-transform 0.2s ease-in-out 0s, width 0.2s ease-in-out 0s, left 0.2s ease-in-out 0s;transition: transform .2s ease-in-out 0s, width 0.2s ease-in-out 0s, left 0.2s ease-in-out 0s;}
.menu i:after {bottom: 0;}
.gnb-all-on {overflow: hidden;}
.gnb-all-on .menu {border-color: transparent;}
.gnb-all-on .menu i:before {left: 50%;width: 0;-webkit-transform: translate(0, 7px);transform: translate(0, 7px);background-color: #333;}
.gnb-all-on .menu i:after {left: 50%;width: 0;-webkit-transform: translate(0, -7px);transform: translate(0, -7px);background-color: #333;}
.menu i > span:before,
.menu i > span:after {content: "";position: absolute;left: 0;top: 50%;display: block;width: 100%;height: 3px;margin-top: -1px;background-color: #333;-webkit-transition: -webkit-transform 0.2s ease-in-out 0s;transition: transform .2s ease-in-out 0s;}
.gnb-all-on .menu i > span:before {-webkit-transform: rotate(45deg);transform: rotate(45deg);}
.gnb-all-on .menu i > span:after {-webkit-transform: rotate(-45deg);transform: rotate(-45deg);}

.gnb-on .menu i:before,
.gnb-on .menu i:after,
.gnb-on .menu i span:before,
.gnb-on .menu i span:after {background-color: #333;}

