#footer .family_site .abtn_fam {padding: 9px 26px 10px 26px;color: #ffffff;white-space: nowrap; text-transform:uppercase;border-radius: 20px;background-color: #485649;}
#footer .family_site .abtn_fam:after {width: 0;height: 0;margin-left: 8px;border-left: 5px solid transparent;border-right: 5px solid transparent;border-top: 5px solid #fff;}
#footer .family_site.on .abtn_fam:after {border-top-width: 0;border-bottom: 5px solid #fff;}
#footer .family_site.on ul {display: block;position: absolute;left: 0;bottom: calc(100% + 10px);}
#footer .family_site ul {display: none;width: 100%;padding: 1rem 2.6rem 1rem 2.6rem;border-radius: 20px;border: 1px solid rgb(210 210 210);background-color: rgb(255 255 255);box-sizing: border-box;}
#footer .family_site li:not(:first-child) {border-top: 1px solid #ddd;}
#footer .family_site li {padding: 10px 0;text-align: left;color: #333;white-space: nowrap;}
#footer .family_site li a:hover {color: #000;}
#footer .copyright {text-transform: uppercase;}
#footer .sns {position: absolute;top: 29px;right: 5px;}
#footer .sns li {position: relative;display: inline-block;margin-left: 6px;}
#footer .sns a {display: block;width: 30px;height: 30px;border-radius: 50%;background-color: #485649;}
#footer .sns a:before {width: 16px;height: 16px;}
#footer .sns .ibtn_ytb:before {background-position: -110px -212px;}
#footer .sns .ibtn_fb:before {background-position: -136px -212px;}
#footer .sns .ibtn_in:before {background-position: -162px -212px;}

/* 유튜브 팝업 */
.pop_wrap {display: none;position: fixed;top: 0;left: 0;right: 0;bottom: 0;z-index: 1000;overflow: auto;text-align: center;background-color: rgb(0 0 0 / 60%);}
.pop_wrap.on {display: block;}
.pop_wrap:before {content: "";display: inline-block;height: 100%;vertical-align: middle;}
.pop_wrap .pop_inwrap {position: relative;display: inline-block;max-width: 100%;vertical-align: middle;margin: 30px;padding: 50px 40px;background-color: rgb(255 255 255);box-sizing: border-box;overflow: auto;}
.pop_wrap .head {margin: 0 22px 34px 0;font-size: 4.2rem;text-align: left;color: #000;padding: 0 0 20px 32px;border-bottom: 1px solid rgb(221 221 221);}
.pop_wrap .head .tit {font-size: inherit;}
.pop_wrap .head .tit:lang(en){letter-spacing:-3px;}
.pop_wrap .head .tit span {color: #007c40;}
.pop_wrap .body {padding: 0 32px;}
.pop_wrap .tab li a{font-size: 2.4rem;color: #c6c5c5;}
.pop_wrap .tab li.on a{color: var(--point);}
.pop_wrap .ibtn_close {position: absolute;top: 30px;right: 30px;width: 40px;height: 40px;}
.pop_wrap .ibtn_close:before {content: "";position: absolute;top: 50%;left: 50%;width: 100%;height: 1px;-ms-transform: rotate(45deg) translate(-50%, -50%);transform: rotate(45deg) translate(-50%, -50%);-ms-transform-origin: 0 0;transform-origin: 0 0;background-color: #888;background-image: none;}
.pop_wrap .ibtn_close:after {content: "";position: absolute;top: 50%;left: 50%;width: 100%;height: 1px;-ms-transform: rotate(45deg) translate(-50%, -50%);transform: rotate(-45deg) translate(-50%, -50%);-ms-transform-origin: 0 0;transform-origin: 0 0;background-color: #888;background-image: none;}

